/* Positions */

/* static */

/* it is a simplest postion that is by default 
without writing it  */

/* relative  */

/* it is same as static  position  because it also 
don't bother with the document flow but the 
top bottom  left right property can be applied   */

/*  absoulute */

/*
 it is just go out of the document flow  to the closest
 relative position in it    
 */

/*  fixed */
/* just like asoulute but it's fixed as a screen  */

/* sticky  */

/* it is quite simple just same as the normal flow until
hits the top left and after that it's stick to the 
position untill parent is still there  
 */

body {
  font-family: sans-serif;
  height: 500vh;
  width: 500vh;
  background-color: yellow;
  position: relative;
}

.box {
  width: 100px;
  height: 100px;
  background-color: red;
  margin: 20px;
}

.box1 {
  position: sticky;
  width: 100%;
  height: 110px;
  background-color: blue;
  top: 0;
}

.box2 {
  position: absolute;
  bottom: 0px;
}

.box2 {
  animation-name: loader;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
